import {upload, createDyn} from '../../api/api.js';
export default {
	data() {
		return {
			imgTemp: this.$store.state.imgTemp,
			dynContent: '',
			location: '所在位置',
			showType: '誰可以看',
			remain: '提醒誰看'
		}
	},
	onNavigationBarButtonTap() {
		upload(this.imgTemp).then(data => {
			if(data != '') {
				createDyn(this.dynContent).then(data => {
					if(data) {
						uni.navigateBack({
							delta: 1
						});
					}
				});
			}
		});
	}
}
